
import Preloader from '../../common/Preloader/Preloader'
import s from './ProfileInfo.module.css'
import ProfileStatus from './ProfileStatus'
import ProfileStatusWithHooks from './ProfileStatusWithHooks'
const ProfileInfo = (props) => {
  if(!props.profile){
    return  <Preloader/>
  }
  const onMainPhotoSelected =(e)=>{
    if (e.target.files.length){
      props.savePhoto(e.target.files[0])
    }
  }
  return (
<div>
  <div className={s.descriptionBlock}>
    <div className={s.photoContainer}>

      <img src={props.profile.photos.small || 'https://bazametrov.ru/uploads/new-agency/default_logo_user.jpg'} className={s.mainPhoto} alt=""/>
      <div className={s.photoCircle}>
        {props.isOwner && <div  className={s.addPhotoOverlay}><input type={"file"} id='photoInput' onChange={onMainPhotoSelected}></input><label htmlFor='photoInput'><span>+</span></label></div>}
      </div>
    </div>
    <div className={s.name}>{props.profile.fullName}</div>
    <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/>
    <div className={s.followers}>
      <div className={s.following}>FOLLOWING: <div><b>0</b></div> </div>
      <div className={s.follower}>FOLLOWERS: <div><b>0</b></div> </div>
    </div>
    <div className={s.aboutMe}> <span>{props.profile.aboutMe} Text text tex tet xt et tx txtet txttet ete t xetetet xt inffffffffffffffffffffffffffffffff </span></div>
    <p>{props.profile.aboutMe}</p>

  </div>
</div>
  )
}
export default ProfileInfo