import s from './MyPosts.module.css'
import Post from './Post/Post'
import React, { useState } from 'react'
import {Formik, Form, Field, ErrorMessage, getIn} from "formik";
import { profileFormSchema } from '../../FormValidation/LoginFormSchema';
import { getUserProfile } from '../../../redux/profile-reducer';
import Preloader from '../../common/Preloader/Preloader';
function getStyles(errors, fieldName) {
  if (getIn(errors, fieldName)) {
    return {
      border: '1px solid red'
    }
  }
}

function CustomInput({ field, form: { errors } }) {
  return <div>
  <textarea {...field} style={getStyles(errors, field.name)} />
  {/*<ErrorMessage name={field.name}/>*/}
  </div>
}

const MyPosts = (props) => {
  const [showModal, setShowModal] = useState(false);
  const handleAddPostClick = () => {
    setShowModal(true);
  };
  const handleCloseModal = () => {
    setShowModal(false);
  };
  if(!props.profile){
    return  <Preloader/>
  }
console.log('RENDER')

let postsElements = props.posts.map( (p )=> {
  return <Post message={p.message} likesCount={p.likesCount} fullName={props.profile.fullName} ava={props.profile.photos.small}/>
})

let onAddPost =(values) =>{
  props.addPost(values)
}
    /*
<div className={s.postsBlock}>
      <h3>My posts</h3> 
      <div>
        <div>
        <AddNewPostForm onAddPost={onAddPost}/>
        <button>Remove</button>
        </div>
      </div>
      <div className={s.posts}>
      {postsElements}

       
      </div>
    </div>
*/
    return (    <div>
<div className={s.container}>
      <div className={s.add_post} onClick={handleAddPostClick}>
      <svg className={s.add_post_icon} xmlns="http://www.w3.org/2000/svg" id="Outline" viewBox="0 0 24 24"><path d="M18.656.93,6.464,13.122A4.966,4.966,0,0,0,5,16.657V18a1,1,0,0,0,1,1H7.343a4.966,4.966,0,0,0,3.535-1.464L23.07,5.344a3.125,3.125,0,0,0,0-4.414A3.194,3.194,0,0,0,18.656.93Zm3,3L9.464,16.122A3.02,3.02,0,0,1,7.343,17H7v-.343a3.02,3.02,0,0,1,.878-2.121L20.07,2.344a1.148,1.148,0,0,1,1.586,0A1.123,1.123,0,0,1,21.656,3.93Z"/><path d="M23,8.979a1,1,0,0,0-1,1V15H18a3,3,0,0,0-3,3v4H5a3,3,0,0,1-3-3V5A3,3,0,0,1,5,2h9.042a1,1,0,0,0,0-2H5A5.006,5.006,0,0,0,0,5V19a5.006,5.006,0,0,0,5,5H16.343a4.968,4.968,0,0,0,3.536-1.464l2.656-2.658A4.968,4.968,0,0,0,24,16.343V9.979A1,1,0,0,0,23,8.979ZM18.465,21.122a2.975,2.975,0,0,1-1.465.8V18a1,1,0,0,1,1-1h3.925a3.016,3.016,0,0,1-.8,1.464Z"/></svg>
        <span className={s.add_post_text}>Add post</span>
      </div>
      {showModal && (
        <div className={s.modal}>
          <div className={s.modal_content}>
            <div className={s.modal_header}>
              <h2>Add post</h2>
              <button onClick={handleCloseModal}>Close</button>
            </div>
            <div className={s.modal_body}>
            <AddNewPostForm onAddPost={onAddPost} handleCloseModal={handleCloseModal}/>
            </div>
          </div>
        </div>
      )}
    </div>
    {postsElements}
</div>
    );
}

const AddNewPostForm = (props) => {
  return (
      <Formik initialValues={{newPostText: ""}}
              onSubmit={(values) => {
                  props.onAddPost(values.newPostText);
                  props.handleCloseModal()      
              }
              }
              validationSchema={profileFormSchema}> 
          {({}) => (
              <Form className={s.form}>
                  <div>
                      <Field component={CustomInput}
                             name={'newPostText'}
                             placeholder={'New post'} 
                             />
                  </div>
                  <div>
                      <button type={'submit'}>Send</button>
                  </div>
              </Form>
          )}
      </Formik>
  )
}
export default MyPosts
