import React from "react"
import { Navigate, useLocation, useNavigate, useParams } from 'react-router-dom';
import { connect } from "react-redux"
import { getStatus, getUserProfile, savePhoto, updateStatus } from "../../redux/profile-reducer"
import Profile from "./Profile"
import { compose } from "redux";
import { withNavigate } from "../../hoc/navigateHoc";
import { withAuthRedirect } from "../../hoc/withAuthRedirect";

class ProfileContainer extends React.Component {
  componentDidMount() {
    console.log('renderPrC')
    let userId = this.props.router.params.userId;
    if (!userId)  { 
    userId = this.props.autorizedUserId;
    }
    this.props.getUserProfile(userId)
    this.props.getStatus(userId)

  }
  //27843
  componentDidUpdate(prevProps,prevState,snapshot) {
    if(this.props.router && this.props.router.params.userId !== prevProps.router.params.userId){
      let userId = this.props.router.params.userId;
      if (!userId)  { 
      userId = this.props.autorizedUserId;
      }
      this.props.getUserProfile(userId)
      this.props.getStatus(userId)  
		} else if(!this.props.router && this.props.router !== prevProps.router){
      let userId = this.props.router.params.userId;
      if (!userId)  { 
      userId = this.props.autorizedUserId;
      }
      this.props.getUserProfile(userId)
      this.props.getStatus(userId)
  }
  }
  render(){

  return (
    <Profile {...this.props}
     profile={this.props.profile}
      status={this.props.status}
      updateStatus={this.props.updateStatus}
      isOwner={!this.props.router.params.userId}
      savePhoto={this.props.savePhoto}/>
  )
  }
}

let mapStateToProps=(state) =>{
  return {
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    autorizedUserId:state.auth.userId,
    isAuth: state.auth.isAuth
  }
}
function withRouter(Component) {
	function ComponentWithRouterProp(props) {
		let location = useLocation();
		let navigate = useNavigate();
		let params = useParams();
		return <Component {...props} router={{ location, navigate, params }} />;
	}
	return ComponentWithRouterProp;
}

export default compose(
  connect(mapStateToProps, { getUserProfile, getStatus, updateStatus,savePhoto }),
  withRouter,
  withAuthRedirect,

  )(ProfileContainer)
  
